import React from "react";
import { Link } from "react-router-dom";
import { Layout, Menu } from "antd";
const { Sider } = Layout;
import { MenuUnfoldOutlined, MenuFoldOutlined } from "@ant-design/icons";
import {
  HOTLINE_REPORTS,
  MANAGE_ORGANIZATIONS,
  OPENLINE_REPORTS,
} from "../../headings";

class IdbSidebar extends React.Component {
  state = {
    collapsed: false,
  };

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  render() {
    let defaultKey = "";
    if (this.props.data?.location) {
      if (this.props.data.location.pathname.includes("hotlinereports")) {
        defaultKey = "1";
      } else if (
        this.props.data.location.pathname.includes("openlinereports")
      ) {
        defaultKey = "2";
      }
    }
    return (
      <>
        {React.createElement(
          this.state.collapsed ? MenuFoldOutlined : MenuUnfoldOutlined,
          {
            className: "trigger d-lg-none d-block",
            onClick: this.toggle,
          }
        )}
        <Sider
          className={
            this.state.collapsed
              ? "d-lg-block w-md-100 sider-width"
              : " d-none d-lg-block w-md-100 sider-width"
          }
          trigger={null}
          collapsible
          collapsed={this.state.collapsed}
        >
          <Menu theme="dark" mode="inline" defaultSelectedKeys={[defaultKey]}>
            <Menu.Item
              key="1"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-manage-org" />
                </span>
              }
            >
              <Link to="/idb/hotlinereports">{HOTLINE_REPORTS}</Link>
            </Menu.Item>
            <Menu.Item
              key="2"
              icon={
                <span className="icon-circle">
                  <span className="icon icon-hotlines" />
                </span>
              }
            >
              {" "}
              <Link to="/idb/openlinereports">{OPENLINE_REPORTS}</Link>
            </Menu.Item>
          </Menu>
        </Sider>
      </>
    );
  }
}

export default IdbSidebar;
