import React from "react";
import { Table, Button, Row, Col, Alert, Spin } from "antd";
import { getManageEmpAsync } from "../../actions/manageEmp";
import Search from "antd/lib/input/Search";
import { connect } from "react-redux";
import "../table/table.scss";
import { FIRST_NAME, LAST_NAME, STATUS, ACCESS, MOBILE } from "../../labels";
import { STATUS_ACTIVE, STATUS_INACTIVE } from "../../values";
import { ASSIGN_USERS_HOTLINES } from "../../errors";
import { BACK, NEXT, SEARCH_USER } from "../../headings";

class CreateHotlineStepTwo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      employeeData: "",
      showAssigntoModal: "false",
      showAlert: false,
      loader: false,
    };
  }

  componentDidMount() {
    const { dispatch } = this.props;
    this.setState({ loader: true });
    dispatch(getManageEmpAsync());
  }

  componentDidUpdate(prevProps) {
    let { manageEmpData } = this.props;
    if (prevProps.manageEmpData != manageEmpData) {
      this.setState(
        {
          employeeData: manageEmpData,
          loader: false,
        },
        () => {
          this.onSearch("");
        }
      );
    }
  }

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === "" || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === "string") {
          return obj[key].toUpperCase().includes(searchKey.toUpperCase());
        }
      });
    });
  }

  onSearch = (value) => {
    const { employeeData } = this.state;
    const filterDt = this.filterIt(employeeData, value);
    this.setState({ filterData: filterDt });
  };

  start = () => {
    this.setState({
      showAssigntoModal: true,
    });
  };

  stepTwoPrev = () => {
    this.props.prev();
  };

  stepTwoNext = () => {
    if (this.props.stepTwoHotLine.chekedValues.length !== 0) {
      this.props.next();
    } else {
      this.setState({ showAlert: true });
    }
  };

  render() {
    const { selectedRowKeys } = this.props.stepTwoHotLine;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.props.handleStepTwoOnSelect,
    };

    const columns = [
      {
        title: FIRST_NAME,
        dataIndex: "firstname",
        key: "firstname",
        width: "22%",
        render: (firstname) => {
          return {
            children: firstname,
            props: {
              "data-title": "First Name",
            },
          };
        },
      },
      {
        title: LAST_NAME,
        dataIndex: "lastname",
        key: "lastname",
        width: "22%",
        render: (lastname) => {
          return {
            children: lastname,
            props: {
              "data-title": "Last Name",
            },
          };
        },
      },
      {
        title: MOBILE,
        dataIndex: "Mobile_Number",
        key: "Mobile_Number",
        width: "21%",
        render: (Mobile_Number) => {
          return {
            children: Mobile_Number,
            props: {
              "data-title": "Mobile Number",
            },
          };
        },
      },
      {
        title: ACCESS,
        dataIndex: "Access",
        render: (res) => {
          let test = "";
          for (let property in res) {
            if (res[property] == true) {
              if (property == "am_web") {
                property = "AM WEB";
              }
              if (property == "org_cc_user") {
                property = "Org CC";
              }
              if (property == "app_user") {
                property = "APP";
              }

              if (test.length == 0) test += property;
              else test += ", " + property;
            }
          }
          return {
            children: <span>{test}</span>,
            props: {
              "data-title": "Access",
            },
          };
        },
        width: "20%",
      },
      {
        title: STATUS,
        dataIndex: "",
        key: "x",
        width: "10%",
        render: (record) => {
          return {
            children:
              record.status === 1 ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>
                  {STATUS_ACTIVE}
                </div>
              ) : (
                <div className="secondary-tag-rounded tag">
                  {STATUS_INACTIVE}
                </div>
              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
    ];
    return (
      <div>
        <Col data-test="createHotlineStepTwo" span={24}>
          <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
            <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
              <Row>
                <Col span={24} lg={9} className="search-box">
                  <Search
                    placeholder={SEARCH_USER}
                    onSearch={this.onSearch}
                  ></Search>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
        {this.state.showAlert && (
          <Alert message={ASSIGN_USERS_HOTLINES} type="error" showIcon />
        )}
        <Spin spinning={this.state.loader} size="large">
          <div id="no-more-tables">
            <Table
              rowSelection={rowSelection}
              columns={columns}
              dataSource={this.state.filterData}
              rowKey="unique_identifier"
            />
          </div>
          <div className="steps-action mt-5">
            <Button
              className="mr-4"
              type="secondary"
              style={{ margin: "0 8px" }}
              onClick={() => this.stepTwoPrev()}
              data-test="steptwoPrev"
            >
              {BACK}
            </Button>
            <Button
              className="md"
              type="primary"
              onClick={() => this.stepTwoNext()}
              data-test="steptwoNext"
            >
              {NEXT}
            </Button>
          </div>
        </Spin>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { manageEmpData } = state.manageEmp;
  const { message } = state.message;
  return {
    manageEmpData,
    message,
  };
}
export default connect(mapStateToProps)(CreateHotlineStepTwo);
