import React, { useState } from "react";
import { Row, Col, Form, Select, Input, Button, DatePicker, Alert } from "antd";
import FloatLabel from "./FloatLabels";
import moment from "moment";
import "../form/form.scss";
import authHeader from "../../services/auth-header";
import { CheckCircleTwoTone } from "@ant-design/icons";
import axios from "axios";
import { BASE_URL } from "../../userconfig";
import {
  MAX_CHAR_LIMIT,
  ORGANIZATION_ID_ALREADY_EXISTS,
  ORGANIZATION_NAME_ALREADY_EXISTS,
  REQUIRED_MESSAGE,
} from "../../errors";
import { DATE, ORGANIZATION_ID, ORGANIZATION_NAME, STATUS } from "../../labels";
import { STATUS_ACTIVE, STATUS_INACTIVE } from "../../values";
import { NEXT } from "../../headings";

const CreateOrgStepOne = ({
  next,
  stepOneOrganizationInfo,
  handleStepOneOnChange,
  handleStepOneOnSelect,
  handleStepOneDatePickerChange,
  orgIdCount,
  orgNameCount,
  setOrgNameAlreadyExists,
  setOrgIDAlreadyExists,
  orgIDAlreadyExists,
  orgNameAlreadyExists,
}) => {
  const [form] = Form.useForm();
  const [error, showError] = useState(false);

  const CheckOrganizationName = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL + "checkOrganization/",
        { organization_name: stepOneOrganizationInfo.organizationName },
        options
      )
      .then((res) => {
        if (res.data) {
          setOrgNameAlreadyExists(res.data[0]);
        }
      });
  };

  const CheckOrganizationID = (e) => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    axios
      .post(
        BASE_URL + "checkOrganizationID/",
        { orgID: stepOneOrganizationInfo.organizationId },
        options
      )
      .then((res) => {
        if (res.data) {
          setOrgIDAlreadyExists(res.data[0]);
        }
      });
  };

  const stepOneNext = (e) => {
    e.preventDefault();
    if (
      stepOneOrganizationInfo.organizationName &&
      stepOneOrganizationInfo.organizationId &&
      stepOneOrganizationInfo.status1 &&
      stepOneOrganizationInfo.s1Date
    ) {
      if (
        orgNameAlreadyExists === "status: false" &&
        orgIDAlreadyExists === "status: false"
      ) {
        showError(false);
        next();
      }
    } else {
      showError(true);
    }
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const { RangePicker } = DatePicker;
  return (
    <Form
      className="input-md floating-label w-100"
      name="validate_other"
      form={form}
    >
      {error && (
        <Form.Item>
          <Alert message={REQUIRED_MESSAGE} type="error" showIcon></Alert>
        </Form.Item>
      )}
      <Col span={24}>
        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel
              label={ORGANIZATION_NAME}
              value={stepOneOrganizationInfo.organizationName}
            >
              <Form.Item className="required" name="organizationName">
                <Input
                  className="input-control"
                  id="organizationName"
                  htmlFor="organizationName"
                  defaultValue={stepOneOrganizationInfo.organizationName}
                  value={stepOneOrganizationInfo.organizationName}
                  onChange={(e) => handleStepOneOnChange(e)}
                  onBlur={(e) => CheckOrganizationName(e)}
                  autoComplete="off"
                  maxLength={80}
                />
                {orgNameAlreadyExists === "status: false" &&
                  stepOneOrganizationInfo.organizationName
                    .replace(/^\s*/, "")
                    .replace(/\s*$/, "").length > 0 &&
                  stepOneOrganizationInfo.organizationName.length > 0 && (
                    <CheckCircleTwoTone
                      className="successCheck"
                      twoToneColor="#52c41a"
                    />
                  )}
                {orgNameAlreadyExists == "status: true" && (
                  <Alert
                    message={ORGANIZATION_NAME_ALREADY_EXISTS}
                    type="error"
                    showIcon
                  ></Alert>
                )}
              </Form.Item>
              {orgNameCount && (
                <Form.Item>
                  <Alert message={MAX_CHAR_LIMIT} type="error"></Alert>
                </Form.Item>
              )}
            </FloatLabel>
          </Col>
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel
              label={ORGANIZATION_ID}
              value={stepOneOrganizationInfo.organizationId}
            >
              <Form.Item
                className="required"
                name="organizationId"
                validateStatus="success"
              >
                <Input
                  className="input-control"
                  id="organizationId"
                  htmlFor="organizationId"
                  defaultValue={stepOneOrganizationInfo.organizationId}
                  value={stepOneOrganizationInfo.organizationId}
                  onChange={(e) => handleStepOneOnChange(e)}
                  onBlur={(e) => CheckOrganizationID(e)}
                  autoComplete="off"
                  maxLength={20}
                />
                {orgIDAlreadyExists === "status: false" &&
                  stepOneOrganizationInfo.organizationId
                    .replace(/^\s*/, "")
                    .replace(/\s*$/, "").length > 0 &&
                  stepOneOrganizationInfo.organizationId.length > 0 && (
                    <CheckCircleTwoTone
                      className="successCheck"
                      twoToneColor="#52c41a"
                    />
                  )}
                {orgIDAlreadyExists == "status: true" && (
                  <Alert
                    message={ORGANIZATION_ID_ALREADY_EXISTS}
                    type="error"
                    showIcon
                  ></Alert>
                )}
              </Form.Item>
              {orgIdCount && (
                <Form.Item>
                  <Alert message={MAX_CHAR_LIMIT} type="error"></Alert>
                </Form.Item>
              )}
            </FloatLabel>
          </Col>
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel label={STATUS} value={stepOneOrganizationInfo.status1}>
              <Form.Item className="required" name="status1">
                <>
                  <Select
                    className="input-control w-100 w-lg-100"
                    value={stepOneOrganizationInfo.status1}
                    htmlFor="status1"
                    onSelect={(value) =>
                      handleStepOneOnSelect(value, "status1")
                    }
                  >
                    <Option value="1">{STATUS_ACTIVE}</Option>
                    <Option value="2">{STATUS_INACTIVE}</Option>
                  </Select>
                </>
              </Form.Item>
            </FloatLabel>
          </Col>
          <Col span={24} md={8} className="gutter-row">
            <FloatLabel label={DATE} value="date">
              <Form.Item className="input-control datepicker-wrapper required">
                <RangePicker
                  className="w-100 "
                  name="s1Date"
                  value={stepOneOrganizationInfo.s1Date}
                  onChange={(date) =>
                    handleStepOneDatePickerChange(date, "s1Date")
                  }
                  disabledDate={disabledDate}
                />
              </Form.Item>
            </FloatLabel>
          </Col>
        </Row>
      </Col>

      <Form.Item>
        <Button type="primary" htmlType="submit" onClick={stepOneNext}>
          {NEXT}
        </Button>
      </Form.Item>
    </Form>
  );
};

export default CreateOrgStepOne;
