import React, { useEffect, useState } from "react";
import {
    Layout,
    Row,
    Col,
    Form,
    Button,
    Input,
    Breadcrumb,
    DatePicker,
    TimePicker,
    notification,
    Spin,
    Alert
} from "antd";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "../form/FloatLabels";
import RichTextEditor from "./Editor";
import Location from "./Location";
import authHeader from "../../services/auth-header";
import axios from "axios";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";
import UploadBroadcast from "./UploadBroadcast";
import { IMAGE, DOCUMENT, CANCEL, UPDATE } from "../../headings";
import { REQUIRED_MESSAGE } from "../../errors";
import momentUtil from "../../utils/momentUtil"
import { BROADCAST_MESSAGE_UPDATE } from "../../messages";



import moment from "moment";

const { Content } = Layout;


const EditBroadcast = (props) => {
    const [loader, setLoader] = useState(false);
    const [form] = Form.useForm();
    const [disabledbtn, setDisabledbtn] = useState(false);
    const [uploadError, showuploadError] = useState(true);
    const [isImage, setIsImage] = useState(false);
    const [error, showError] = useState(false);
    const [broadcast, setBroadcast] = useState({
        messageTittle: "",
        meesage: "",
        startDate: "",
        startTime: "",
        attachmentType: "",
        attachments: [],
        location_latitude: "",
        location_longitude: "",
        broadCastImage: "",
        broadCastDocument: "",
        previewVisible: false,
        previewImage: "",
        previewTitle: "",
        statusId: "",
        mapLoction: "",
        brodcastId:""
    })
    const [api, contextHolder] = notification.useNotification();
    const broadcastId = props.computedMatch?.params.id;
    useEffect(() => {
        const token = authHeader();
        const options = {
            headers: {
                Authorization: token,
                Accept: "application/json, text/plain",
                "Content-Type": "application/json;charset=UTF-8",
            },
        };
        setLoader(true);
        axios.get(BASE_URL + `getBroadcast/${broadcastId}`, options).then((res) => {
            const data = res.data;
            setBroadcast({
                ...broadcast,
                brodcastId:data.broadcast_id,
                messageTittle: data.title,
                meesage: data.message,
                startDate: moment(data.scheduled_date, "YYYY-MM-DD"),
                startTime: moment(data.scheduled_time, "HH:mm:ss"),
                location_longitude: data.location_longitude,
                location_latitude: data.location_latitude,
                statusId: data.status_id
            });
            var broadCastImageArray = [];
            var broadCastDocumentArray = [];
            data.broadcast_images?.map((item) => {
                var key = "broadcastimg" + Math.floor(Math.random() * 100)
                broadCastImageArray.push({ uid: key, name: item.name, status: "done", url: item.url })
            })

            data.broadcast_documents?.map((item) => {
                var key = "broadcastdoc" + Math.floor(Math.random() * 100)
                broadCastDocumentArray.push({ uid: key, name: item.name, status: "done", url: item.url })
            })
            setBroadcast((prevState) => {
                return {
                    ...prevState,
                    broadCastImage: broadCastImageArray,
                    broadCastDocument: broadCastDocumentArray
                }
            })

            if (data.broadcast_images.length + data.broadcast_documents.length == 4) {
                setDisabledbtn(true)
            }

            form.setFieldsValue({
                messageTittle: data.title,
            });
            setLoader(false);
        })
            .catch((error) => {
                setLoader(false);
            })
    }, [])

    const onBroadcastChange = (e) => {
        console.log(e.target.value)
        setBroadcast({ ...broadcast, [e.target.id]: e.target.value })
    };

    const onBroadMessageChange = (content, name) => {
        setBroadcast((prevState) => {
            return { ...prevState, [name]: content }
        })
    };

    const onChangeStartDate = (date, name) => {
        console.log(date)
        setBroadcast({ ...broadcast, [name]: date })
    };

    const disabledDate = (current) => {
        return current && current < moment().startOf("day");
    };

    const onChangeStartTime = (time, name) => {
        console.log(time)
        setBroadcast({ ...broadcast, [name]: time })
    };

    const handleStepDocumentUpload = (value, name) => {
        chekMaxUpload();
        if (value.file.status === "removed") {
            showuploadError(true);
            chekMaxUpload(value.file.status);
        }
        let fileList = value.fileList;
        fileList.forEach(function (file, index) {
            if (!file.uid.includes("broadcastdoc")) {
             let reader = new FileReader();
                reader.onload = (e) => {
                    file.base64 = e.target.result;
                    file.response = "Uploaded successfully";
                };
                reader.readAsDataURL(file.originFileObj);
           } 
        });
        let docname = "broadCastDocument";
        setBroadcast({ ...broadcast, [docname]: fileList });
    };

    const chekMaxUpload = () => {
        let documentLength = broadcast.broadCastDocument.length
        let imageLength = broadcast.broadCastImage.length
        let docLength= (imageLength + documentLength);
        console.log((docLength-1))
         if(status ==="removed" ){
            if ((docLength-1) === 4) {
              setDisabledbtn(true);
            } else {
              setDisabledbtn(false);
             }
        }else{
            if (docLength === 3) {
                setDisabledbtn(true);
            } else {
                setDisabledbtn(false);
             }
        }
    }
    const checkDoucmentType = (fileType) => {
        const isImage = fileType.includes("image");
        if (isImage) {
            setIsImage(true);
        } else {
            setIsImage(false);
        }
    };
    const handleStepOneUpload = (value, name) => {
        chekMaxUpload();
        if (value.file.status === "removed") {
            showuploadError(true);
            chekMaxUpload(value.file.status);
          }
        let fileList = value.fileList;
        fileList.forEach(function (file, index) {
            if (!file.uid.includes("broadcastimg")) {
                let reader = new FileReader();
                reader.onload = (e) => {
                    file.base64 = e.target.result;
                    file.response = "Uploaded successfully";
                };
                reader.readAsDataURL(file.originFileObj);
            }
        });
        setBroadcast({ ...broadcast, [name]: fileList });
    };

    const handlePollImagePreview = async (file) => {
        if (file.uid.includes("broadcast")) {
            convertImgToBase64(file.url, function (base64Img) {
                setBroadcast({
                    ...broadcast,
                    previewImage: base64Img,
                    previewVisible: true,
                    previewTitle: file.name,
                });
            });

        } else {
            file.preview = await getBase64(file.originFileObj);
            setBroadcast({
                ...broadcast,
                previewImage: file.url || file.preview,
                previewVisible: true,
                previewTitle:
                    file.name || file.url.substring(file.url.lastIndexOf("/") + 1),
            });
        }
    };

    function convertImgToBase64(url, callback, outputFormat) {
        var canvas = document.createElement("CANVAS");
        var ctx = canvas.getContext("2d");
        var img = new Image();
        img.crossOrigin = "Anonymous";
        img.onload = function () {
            canvas.height = img.height;
            canvas.width = img.width;
            ctx.drawImage(img, 0, 0);
            var dataURL = canvas.toDataURL(outputFormat || "image/png");
            callback.call(this, dataURL);
            // Clean up
            canvas = null;
        };
        img.src = url;
    }

    const handleCancel = (value) => setBroadcast({ ...broadcast, previewVisible: false });

    const getBase64 = (file) => {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => resolve(reader.result);
            reader.onerror = (error) => reject(error);
        });
    };

    const handleOnRemove = (value, name) => {
        // setPoll({ ...poll, pollImageValidation: true });
    };

    const handleBeforeDocUpload = (file, name) => {
        let xlsxfileExt = file.name.includes(".xls");
        let pptxfileExt = file.name.includes(".pptx");
        let docfileExt = file.name.includes(".doc");
        let csvfileExt = file.name.includes(".csv");

        const isValidDoc =
            file.type === "application/pdf" ||
            xlsxfileExt ||
            pptxfileExt ||
            docfileExt ||
            csvfileExt;
        if (!isValidDoc) {
            showuploadError(false);
            message.error(`${file.name} is not a supported format file type.`);
        } else {
            showuploadError(true);
        }

        const isLt10M = file.size / 1024 / 1024 < 10;
        if (isValidDoc) {
            if (!isLt10M && isValidDoc) {
                showuploadError(false);
                message.error("Document must smaller than 10MB!");
            } else {
                showuploadError(true);
            }
        }

        return (isValidDoc && isLt10M);
    }

    const getDisabledHours  =(value)=>{
        var hours = [];
        if(moment().isSame(broadcast.startDate, 'day')){
           for(var i =0; i < moment().hour(); i++){
              hours.push(i);
           }
        }
        return hours;
      }
    
      const getDisabledMinutes  =(selectedHour)=>{
        var minutes= [];
        if(moment().isSame(broadcast.startDate, 'day')){
             if (selectedHour === moment().hour()){
                for(var i =0; i < moment().minute(); i++){
                    minutes.push(i);
                }
            }
        }
        return minutes;
      }
    
      const getDisabledSeconds = (selectedHour, selectedMinute) => {
        var second= [];
        if (selectedHour === moment().hour() && selectedMinute ==  moment().minute() ){
            for(var i =0; i < moment().second(); i++){
              second.push(i);
            }
        }
        return second;
     }

    const handleBeforeUpload = (file, name) => {
        const isImage =
            file.type === "image/jpeg" ||
            file.type === "image/png" ||
            file.type === "image/gif";

        const isLt10M = file.size / 1024 / 1024 < 10;

        if (!isImage) {
            showuploadError(false);
            message.error(`${file.name} is not a supported format file type.`);
        } else {
            showuploadError(true);
        }
        if (isImage) {
            if (!isLt10M && isImage) {
                showuploadError(false);
                message.error("Image must smaller than 5MB!");
            } else {
                showuploadError(true);
            }
        }

        return (isImage && isLt10M);
    };

    const setLocation = (longitude, latitude, name) => {
        var obj = {
            location_latitude: latitude,
            location_longitude: longitude,
            address:"New Bombay"
        }
        setBroadcast({...broadcast, mapLoction: obj})
    }

    const onUpdate = (e) => {
        e.preventDefault();
        if (uploadError && broadcast.messageTittle && broadcast.messageTittle.replace(/^\s*/, "").replace(/\s*$/, "").length > 0
            && broadcast.startTime && broadcast.meesage && broadcast.startDate) {
            updateBroadcast();
            showError(false);
        } else {
            showError(true);
        }
    }

    const updateBroadcast = () => {
        setLoader(true);
        const token = authHeader();
        const options = {
            headers: {
                Authorization: token,
                Accept: "application/json, text/plain",
                "Content-Type": "application/json;charset=UTF-8",
            },
        };

        var documentArray = [], imgArray = [];
        broadcast.broadCastDocument?.map((item) => {
            if (item.uid.includes("broadcastdoc")) {
                convertImgToBase64(item.url, function (base64Img) {
                    documentArray.push({ name:item.name ,url:base64Img })
                })
            } else { documentArray.push({name:item.name, url:item.base64} )}
            console.log("documentArray", documentArray)
        })

        broadcast.broadCastImage?.map((item) => {
            if (item.uid.includes("broadcastimg")) {
                convertImgToBase64(item.url, function (base64Img) {
                    imgArray.push(base64Img)
                })
            } else {
                imgArray.push(item.base64)
            }
            console.log("imgArray", imgArray)
        })


        const attachments = { documents: documentArray, images: imgArray }
        const data = {
            title: broadcast.messageTittle,
            message: broadcast.meesage,
            attachments: attachments,
            location: broadcast.mapLoction,
            scheduled_date: momentUtil.formatAsDDMMYYYY(broadcast.startDate),
            scheduled_time: momentUtil.formatAsHHmmss(broadcast.startTime),
            status_id: broadcast.statusId
        }
        console.log(data)
        axios.put(BASE_URL + `broadcast/`+broadcast.brodcastId+`/`, data, options)
            .then(() => {
                const placement = "bottomLeft";
                api.info({
                    description: BROADCAST_MESSAGE_UPDATE,
                    placement,
                });
                setLoader(false);
                window.location = "/orgcc/broadcastmessages";
            })
            .catch((error) => {
                setLoader(false);
            });
    }

    const onCancel = () => {
        window.location = "/orgcc/broadcastmessages";
    }

    return (
        <>
            {contextHolder}
            <React.Fragment>
                <React.Fragment>
                    {" "}
                    <Row className="inner-header">
                        <LoggedinHeader amORGUser />
                    </Row>
                    {console.log(broadcast)}
                    <Row className="content-center pt-0">
                        <Layout>
                            <Layout>
                                <OrgSidebar amORGUser />
                                <Content>
                                    <Spin spinning={loader} size="large">

                                        <Row className="py-3">
                                            <h1 className="mb-0">Edit Broadcast Message</h1>
                                        </Row>
                                        <div className="mid-content">
                                            <Row>
                                                <Col span={24}>
                                                    <Form
                                                        className="input-md floating-label w-100"
                                                        name="validate_other"
                                                        form={form}
                                                    >
                                                        {error && (
                                                            <Form.Item>
                                                                <Alert
                                                                    message={REQUIRED_MESSAGE}
                                                                    type="error"
                                                                    showIcon
                                                                ></Alert>
                                                            </Form.Item>
                                                        )}
                                                        <Col span={24}>
                                                            <Row>
                                                                <Col span={24} md={18} className="gutter-row">
                                                                    <FloatLabel label="Message Title" value={broadcast.messageTittle}>
                                                                        <Form.Item
                                                                            className="required"
                                                                            name="messageTittle"
                                                                            id="messageTittle"
                                                                            validateStatus="success"
                                                                        >
                                                                            <Input
                                                                                className="input-control"
                                                                                id="messageTittle"
                                                                                name="messageTittle"
                                                                                htmlFor="helptext"
                                                                                autoComplete={"off"}
                                                                                value={broadcast.messageTittle}
                                                                                maxLength={80}
                                                                                onChange={(e) => { onBroadcastChange(e) }}
                                                                            />
                                                                        </Form.Item>
                                                                    </FloatLabel>
                                                                </Col>
                                                            </Row>
                                                        </Col>
                                                        <Col span={24}>
                                                            <Row>
                                                                <Col span={24} md={18} className="gutter-row">
                                                                    <FloatLabel label="Message" value={broadcast.meesage}>
                                                                        <Form.Item
                                                                            className="required"
                                                                            name="message"
                                                                            validateStatus="success"
                                                                        >   
                                                                            {console.log(broadcast.meesage)}
                                                                            <RichTextEditor
                                                                                value={broadcast.meesage}
                                                                                onChange={(content) => onBroadMessageChange(content, "meesage")}
                                                                                id="message"
                                                                            />

                                                                        </Form.Item>
                                                                    </FloatLabel>
                                                                </Col>
                                                            </Row>
                                                        </Col>
                                                        <Col span={24} className="gutter-row">
                                                            <Row
                                                                className="align-items-center d-flex"
                                                                gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}
                                                            >
                                                                <Col span={24} md={9} lg={6} className="gutter-row">
                                                                    <FloatLabel label="Select Date" value={broadcast.startDate}>
                                                                        <Form.Item
                                                                            className="required mb-0"
                                                                            name="startDate"
                                                                        >
                                                                            {console.log(broadcast.startDate)}
                                                                            <DatePicker
                                                                                disabledDate={disabledDate}
                                                                                name="startDate"
                                                                                value={broadcast.startDate}
                                                                                onChange={(date) => onChangeStartDate(date, "startDate")}
                                                                            />
                                                                        </Form.Item>
                                                                    </FloatLabel>
                                                                </Col>
                                                                <Col span={24} md={9} lg={6} className="gutter-row">
                                                                    <FloatLabel label="Select Time" value={broadcast.startTime}>
                                                                        <Form.Item
                                                                            className="required timepicker mb-0"
                                                                            name="time"
                                                                        >
                                                                            {console.log(broadcast.startTime)}

                                                                            <TimePicker
                                                                                value={broadcast.startTime}
                                                                                onChange={(value) => onChangeStartTime(value, "startTime")}
                                                                                disabledHours={getDisabledHours}
                                                                                disabledMinutes={getDisabledMinutes}
                                                                                disabledSeconds ={getDisabledSeconds}
                                                                                defaultOpenValue={moment(
                                                                                    "00:00:00",
                                                                                    "HH:mm:ss"
                                                                                )}
                                                                            />
                                                                        </Form.Item>
                                                                    </FloatLabel>
                                                                </Col>
                                                                <Col span={24} className="6gutter-row mt-3">
                                                                    <h6>Upload</h6>
                                                                    <Form.Item
                                                                        name="help text"
                                                                        className="upload-list mt-3"
                                                                    >
                                                                        <Row>
                                                                            <UploadBroadcast
                                                                                file={broadcast.broadCastImage}
                                                                                handleStepOneUpload={handleStepOneUpload}
                                                                                handleBeforeUpload={handleBeforeUpload}
                                                                                handleOnRemove={handleOnRemove}
                                                                                handlePollImagePreview={handlePollImagePreview}
                                                                                handleCancel={handleCancel}
                                                                                broadcast={broadcast}
                                                                                uploadtitle={IMAGE}
                                                                                disabledbutton={disabledbtn}
                                                                            />

                                                                            <UploadBroadcast
                                                                                file={broadcast.broadCastDocument}
                                                                                handleStepOneUpload={handleStepDocumentUpload}
                                                                                handleBeforeUpload={handleBeforeDocUpload}
                                                                                handleOnRemove={handleOnRemove}
                                                                                handlePollImagePreview={handlePollImagePreview}
                                                                                handleCancel={handleCancel}
                                                                                broadcast={broadcast}
                                                                                uploadtitle={DOCUMENT}
                                                                                disabledbutton={disabledbtn}

                                                                            />
                                                                            <Location
                                                                                setLocation={setLocation} />
                                                                        </Row>
                                                                    </Form.Item>
                                                                </Col>
                                                            </Row>
                                                            <div className="mt-5">
                                                                <Button
                                                                    type="primary"
                                                                    className="md mr-4"
                                                                    onClick={onUpdate}
                                                                >
                                                                    {UPDATE}
                                                                </Button>
                                                                <Button
                                                                    type="secondary"
                                                                    type="secondary"
                                                                    htmlType="submit"
                                                                    onClick={onCancel}
                                                                >
                                                                    {CANCEL}
                                                                </Button>
                                                            </div>
                                                        </Col>
                                                    </Form>
                                                </Col>
                                            </Row>
                                        </div>
                                    </Spin>
                                </Content>
                            </Layout>
                        </Layout>
                    </Row>
                </React.Fragment>
            </React.Fragment>
        </>
    );
};

export default EditBroadcast;


