import React from "react";
import { Pie } from "@reactchartjs/react-chart.js";

const PieChart = (props) => {
  const data = {
    labels: ["Unresolved Messages", "Resolved Messages"],
    datasets: [
      {
        label: "",
        data: [
          props.totalMessages - props.resolvedMessages,
          props.resolvedMessages,
        ],
        backgroundColor: ["rgba(234, 80, 55)", "rgba(32, 50, 92)"],
        borderColor: ["rgba(255, 255, 255)", "rgba(255, 255, 255)"],
        borderWidth: 3,
      },
    ],
  };
  const options = {
    title: {
      display: true,
      text: props.chartTitle,
    },
  };
  return (
    <>
      <Pie id="pieChart" data={data} options={options} />
    </>
  );
};

export default PieChart;
