import React, { useState, useEffect } from "react";
import { Layout, Row, Col, Radio, DatePicker, Spin } from "antd";
import { LoggedinHeader } from "../../containers";
import VerticalBar from "./VerticalChart";
import PieChart from "./PieChart";
import DoughNutChart from "./DoughNutChart";
import { DownloadOutlined, DownOutlined } from "@ant-design/icons";
import MultiSelection from "./MultiSelection";
import IdbSidebar from "./IdbSidebar";
import * as AppStorage from "../../utils/AppStorage";
import {
  CHOOSE_DATE_RANGE,
  WEEKLY,
  MONTHLY,
  YEARLY,
  TOTAL_MESSAGES,
  MESSAGES_RESOLVED,
  AVERAGE_RESOLVED_TIME,
  RESOLVED_MESSAGES,
  HOTLINE_REPORTS,
  OPENLINE_REPORTS,
  MESSAGES_BASED_ON_URGENCY_LEVEL,
  CONVERSATION_COUNT,
} from "../../headings";
import authHeader from "../../services/auth-header";
import axios from "axios";
import moment from "moment";
import {
  HOTLINE_MESSAGES_FROM,
  TO,
  HOTLINE_MESSAGES_REPORTING,
  OPENLINE_MESSAGES_REPORTING,
  OPENLINE_MESSAGES_FROM,
} from "../../messages";
import { BASE_URL } from "../../userconfig";
import {
  JANUARY,
  FEBRUARY,
  MARCH,
  APRIL,
  MAY,
  JUNE,
  JULY,
  AUGUST,
  SEPTEMBER,
  OCTOBER,
  NOVEMBER,
  DECEMBER,
  DAY,
  MONTH,
  YEAR,
} from "../../values";
import html2canvas from "html2canvas";
import { jsPDF } from "jspdf";
import ReportsPopover from "./ReportsPopover";

var CryptoJS = require("crypto-js");
var months = [
  JANUARY,
  FEBRUARY,
  MARCH,
  APRIL,
  MAY,
  JUNE,
  JULY,
  AUGUST,
  SEPTEMBER,
  OCTOBER,
  NOVEMBER,
  DECEMBER,
];
const { Content } = Layout;
const { RangePicker } = DatePicker;
const Reports = (props) => {
  const [loader, setLoader] = useState(false);
  const [date, setDate] = useState("");
  const [radioValue, setRadioValue] = useState("weekly");
  const [period, setPeriod] = useState("");
  const [selectedLines, setSelectedLines] = useState([]);
  const [lines, setLines] = useState([]);
  const [dataPoints, setDataPoints] = useState([]);
  const [dateRange, setDateRange] = useState("");
  const [years, setYears] = useState([]);
  const [totalMessagesCount, setTotalMessagesCount] = useState("0");
  const [closedConversationsCount, setClosedConversationsCount] = useState("0");
  const [averageResolvedTime, setAverageResolvedTime] = useState("0");
  const [urgencyLevels, setUrgencyLevels] = useState({});
  const [report, setReport] = useState({});
  const Messages_From = props.path.includes("hotline")
    ? HOTLINE_MESSAGES_FROM
    : OPENLINE_MESSAGES_FROM;
  const Messages_Reporting = props.path.includes("hotline")
    ? HOTLINE_MESSAGES_REPORTING
    : OPENLINE_MESSAGES_REPORTING;
  const [chartTitle, setChartTitle] = useState(Messages_Reporting);

  useEffect(() => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const path = props.path.includes("hotline")
      ? "getUserallHotlines/"
      : "getUserAllOpenlines/";
    axios
      .get(
        BASE_URL +
          path +
          parseInt(
            CryptoJS.AES.decrypt(user.user_data.user_id, "anonymoose").toString(
              CryptoJS.enc.Utf8
            )
          ),
        options
      )
      .then((response) => {
        const lines = [];
        response.data.map((value) => {
          lines.push({
            line_name: props.path.includes("hotline")
              ? value.hotline_name
              : value.openline.name,
            line_id: props.path.includes("hotline")
              ? value.hotline_id
              : value.openline.openline_id,
          });
        });
        setLines(lines);
      });
    setDate("");
    setSelectedLines([]);
    setLoader(false);
    setPeriod("");
    setDateRange("months");
    setChartTitle(Messages_Reporting);
    setTotalMessagesCount("0");
    setClosedConversationsCount("0");
    setAverageResolvedTime("0");
  }, [props.path]);

  useEffect(() => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    setLoader(true);
    let line_ids = [];
    if (
      selectedLines.length === 0 ||
      (selectedLines.length === 1 && selectedLines[0].line_name === "All")
    ) {
      line_ids = lines.map((value) => value.line_id);
    } else {
      line_ids = selectedLines.map((value) => value.line_id);
    }
    let yearValue = "";
    let monthValue = "";
    let dayValue = "";
    if (date?.length === 0 || !date) {
      yearValue = "0";
      monthValue = "1";
      dayValue = "0";
      setDateRange("months");
      setChartTitle(Messages_Reporting);
    } else {
      yearValue = dateRange === "years" ? "1" : "0";
      monthValue = dateRange === "months" ? "1" : "0";
      dayValue = dateRange === "days" ? "1" : "0";
    }
    let input = {};
    const now = new Date();
    const past90DaysTimeStamp = now.setDate(now.getDate() - 90);
    const past90Days = new Date(past90DaysTimeStamp);
    if (line_ids.length > 0) {
      if (props.path.includes("hotline")) {
        input = {
          years: yearValue,
          month: monthValue,
          days: dayValue,
          filter: {
            start_date:
              date && date !== ""
                ? moment(date[0], moment.defaultFormat)
                : past90Days,
            end_date:
              date && date !== ""
                ? moment(date[1], moment.defaultFormat)
                : new Date(),
            hotline_ids: line_ids,
          },
        };
      } else {
        input = {
          years: yearValue,
          month: monthValue,
          days: dayValue,
          filter: {
            start_date:
              date && date !== ""
                ? moment(date[0], moment.defaultFormat)
                : past90Days,
            end_date:
              date && date !== ""
                ? moment(date[1], moment.defaultFormat)
                : new Date(),
            openline_ids: line_ids,
          },
        };
      }

      const path = props.path.includes("hotline")
        ? "hotlineReporting/"
        : "openlineReporting/";
      axios
        .post(BASE_URL + path, input, options)
        .then((res) => {
          setTotalMessagesCount(res.data.total_conversation_count);
          if (res.data.avg_time_to_resolve_conversation > 0) {
            setAverageResolvedTime(
              Math.round(res.data.avg_time_to_resolve_conversation) + 1
            );
          } else {
            setAverageResolvedTime(res.data.avg_time_to_resolve_conversation);
          }
          setClosedConversationsCount(res.data.closed_conversation_count);
          setUrgencyLevels(res.data.urgency_level);
          setReport(res.data.report);
          setLoader(false);
        })
        .catch((error) => setLoader(false));
    }
  }, [lines, date, selectedLines]);

  const onRadioChange = (e) => {
    setRadioValue(e.target.value);
  };

  const downloadChartImage = (type) => {
    var url_base64jp = "";
    var a = "";
    if (type === "barChart") {
      /* Get image of canvas element */
      url_base64jp = document.getElementById("barChart").toDataURL("image/jpg");
      /* Get download button (tag: <a></a>) */
      a = document.getElementById("downloadBarChart");
    } else if (type === "doughNutChart") {
      url_base64jp = document
        .getElementById("doughNutChart")
        .toDataURL("image/jpg");
      a = document.getElementById("downloadDoughNutChart");
    } else {
      url_base64jp = document.getElementById("pieChart").toDataURL("image/jpg");
      a = document.getElementById("downloadPieChart");
    }
    /* Insert chart image url to download button (tag: <a></a>) */
    a.href = url_base64jp;
  };

  const downloadPDF = () => {
    let element = document.getElementById("barChart");
    html2canvas(element).then((canvas) => {
      const imgData = canvas.toDataURL("image/png");
      const pdf = new jsPDF();
      pdf.addImage(imgData, "JPEG", 0, 0, 200, 100);
      const filename = props.path?.includes("hotline")
        ? "hotline-report.pdf"
        : "openline-report.pdf";
      pdf.save(filename);
    });
  };

  function convertChartDataToCSV(data) {
    var result, ctr, keys, columnDelimiter, lineDelimiter, data;
    data = data || null;
    if (data == null || !data.length) {
      return null;
    }
    columnDelimiter = ",";
    lineDelimiter = "\n";
    keys = Object.keys(data[0]);
    result = "";
    result += keys.join(columnDelimiter);
    result += lineDelimiter;
    data.forEach(function (item) {
      ctr = 0;
      keys.forEach(function (key) {
        if (ctr > 0) result += columnDelimiter;
        result += item[key];
        ctr++;
      });
      result += lineDelimiter;
    });
    return result;
  }

  const sendBackData = (x_data, y_data) => {
    setDataPoints([x_data, y_data]);
  };

  const downloadCSV = () => {
    let points = [];
    let x_index = MONTH;
    dataPoints[0].forEach((xpoint, index) => {
      dataPoints[1].forEach((ypoint, key) => {
        if (index === key) {
          if (dateRange === "months") {
            x_index = MONTH;
          } else if (dateRange === "years") {
            x_index = YEAR;
          } else {
            x_index = DAY;
          }
          points.push({ [x_index]: xpoint, [CONVERSATION_COUNT]: ypoint });
        }
      });
    });
    var data, filename, link;
    var csv = "";
    csv += convertChartDataToCSV(points);
    if (csv == null) return;
    filename = props.path?.includes("hotline")
      ? "hotline-report.csv"
      : "openline-report.csv";
    if (!csv.match(/^data:text\/csv/i)) {
      csv = "data:text/csv;charset=utf-8," + csv;
    }
    data = encodeURI(csv);
    link = document.createElement("a");
    link.setAttribute("href", data);
    link.setAttribute("download", filename);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const showDownloadOptions = (value) => {
    if (value === "pdf") {
      downloadPDF();
    } else {
      downloadCSV();
    }
  };

  const handleDatePickerChange = (date) => {
    setDate(date);
    if (date) {
      var startMonth = parseInt(date[0].format("M"));
      var startDay = parseInt(date[0].format("D"));
      var startYear = parseInt(date[0].format("YYYY"));
      const start_date = [startDay, startMonth - 1, startYear];
      var endMonth = parseInt(date[1].format("M"));
      var endDay = parseInt(date[1].format("D"));
      var endYear = parseInt(date[1].format("YYYY"));
      const end_date = [endDay, endMonth - 1, endYear];
      setYears([startYear, endYear]);
      const differenceInDates = Math.abs(date[0].diff(date[1], "days"));
      if (differenceInDates <= 31) {
        setDateRange("days");
      } else if (differenceInDates > 30 && differenceInDates < 365) {
        setDateRange("months");
      } else {
        setDateRange("years");
      }
      if (startYear !== endYear) {
        if (startYear > endYear) {
          setChartTitle(
            Messages_From +
              months[end_date[1]] +
              " " +
              end_date[0] +
              ", " +
              end_date[2] +
              TO +
              months[start_date[1]] +
              " " +
              start_date[0] +
              ", " +
              start_date[2]
          );
          setPeriod("greater year");
        } else {
          setChartTitle(
            Messages_From +
              months[start_date[1]] +
              " " +
              start_date[0] +
              ", " +
              start_date[2] +
              TO +
              months[end_date[1]] +
              " " +
              end_date[0] +
              ", " +
              end_date[2]
          );
          setPeriod("lesser year");
        }
      } else if (startYear === endYear && startMonth !== endMonth) {
        if (startMonth > endMonth) {
          setChartTitle(
            Messages_From +
              months[end_date[1]] +
              " " +
              end_date[0] +
              TO +
              months[start_date[1]] +
              " " +
              start_date[0]
          );
          setPeriod("greater month");
        } else {
          setChartTitle(
            Messages_From +
              months[start_date[1]] +
              " " +
              start_date[0] +
              TO +
              months[end_date[1]] +
              " " +
              end_date[0]
          );
          setPeriod("lesser month");
        }
      } else if (
        startYear === endYear &&
        startMonth === endMonth &&
        startDay !== endDay
      ) {
        if (startDay > endDay) {
          setChartTitle(
            Messages_From +
              months[endMonth - 1] +
              " " +
              end_date[0] +
              TO +
              months[startMonth - 1] +
              " " +
              start_date[0]
          );
          setPeriod("greater day");
        } else {
          setChartTitle(
            Messages_From +
              months[startMonth - 1] +
              " " +
              start_date[0] +
              TO +
              months[endMonth - 1] +
              " " +
              end_date[0]
          );
          setPeriod("lesser day");
        }
      } else if (
        startYear === endYear &&
        startMonth === endMonth &&
        startDay === endDay
      ) {
        setChartTitle(
          Messages_From +
            months[startMonth - 1] +
            " " +
            start_date[0] +
            TO +
            months[endMonth - 1] +
            " " +
            end_date[0]
        );
        setPeriod("same day");
      }
    } else {
      setPeriod("");
    }
  };

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amIDBUser />
      </Row>
      <Row className="content-center pt-0" data-test="reports">
        <Layout>
          <Layout>
            <IdbSidebar amIDBUser data={props} />
            <Content className="">
              <Row className="py-3">
                <h1 className="mb-0">
                  {props.path.includes("hotline")
                    ? HOTLINE_REPORTS
                    : OPENLINE_REPORTS}
                </h1>
              </Row>
              <Spin spinning={loader} size="large">
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={12} className="search-row mt-2 mt-lg-0">
                      <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                        <Col
                          span={24}
                          lg={12}
                          className="gutter-row mt-2 mt-lg-0"
                        >
                          <MultiSelection
                            lines={lines}
                            setSelectedLines={setSelectedLines}
                            data={props}
                            selectedLines={selectedLines}
                          />
                          <DownOutlined className="down-arrow-ico" />
                        </Col>
                        <Col
                          span={24}
                          lg={12}
                          className="gutter-row mt-2 mt-lg-2 mt-xl-0"
                        >
                          <h6 className="font-14 pl-3">{CHOOSE_DATE_RANGE}</h6>
                          <Col span={24} className="mt-2 mt-lg-0">
                            <RangePicker
                              name="date"
                              value={date}
                              onChange={(value) =>
                                handleDatePickerChange(value)
                              }
                            />
                          </Col>
                        </Col>
                      </Row>
                    </Col>
                    <Col span={24} lg={12} className="mt-2 mt-lg-0">
                      <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }} className="justify-content-end">
                        {/* <Col
                          span={24}
                          lg={16}
                          className="gutter-row d-flex align-items-center justify-content-left justify-content-lg-end chart-view mt-3 mt-lg-0 mb-3 mb-lg-0"
                        >
                          <Radio.Group
                            onChange={onRadioChange}
                            value={radioValue}
                          >
                            <Radio.Button value="weekly">{WEEKLY}</Radio.Button>
                            <Radio.Button value="monthly">
                              {MONTHLY}
                            </Radio.Button>
                            <Radio.Button value="yearly">{YEARLY}</Radio.Button>
                          </Radio.Group>
                        </Col> */}
                        <Col
                          span={24}
                          lg={8}
                          className="gutter-row d-flex align-items-center justify-content-end"
                        >
                          <ReportsPopover
                            showDownloadOptions={showDownloadOptions}
                          />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </Col>
                <Row>
                  <Col span={24} className="gutter-row">
                    <div className="mid-content mt-4 h-auto d-flex justify-content-center align-items-center text-center">
                      <Col span={8} className="divider-right font-weight-md6">
                        <div className="font-18">{totalMessagesCount}</div>
                        <div className="font-12">{TOTAL_MESSAGES}</div>
                      </Col>
                      <Col span={8} className="divider-right font-weight-md6">
                        <div className="font-18">
                          {closedConversationsCount}
                        </div>
                        <div className="font-12">{MESSAGES_RESOLVED}</div>
                      </Col>
                      <Col span={8} className="font-weight-md6">
                        <div className="font-18">{averageResolvedTime}d</div>
                        <div className="font-12">{AVERAGE_RESOLVED_TIME}</div>
                      </Col>
                    </div>
                  </Col>
                </Row>
                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                  <Col span={24} lg={24} className="gutter-row">
                    <div className="mid-content mt-4">
                      <Col span={24} className="gutter-row">
                        <Row className="d-inine-flex justify-content-between align-items-center mb-3">
                          <h6 className="font-weight-md6 font-14 mb-0">
                            {period !== "" ? chartTitle : Messages_Reporting}
                          </h6>
                          <span>
                            <a
                              id="downloadBarChart"
                              download={
                                props.path?.includes("hotline")
                                  ? "Hotline-report.jpg"
                                  : "Openline-report.jpg"
                              }
                              href=""
                            >
                              <DownloadOutlined
                                className="font-18 cursor-pointer"
                                onClick={() => downloadChartImage("barChart")}
                              />
                            </a>
                          </span>
                        </Row>
                        <VerticalBar
                          months={months}
                          selectedLines={selectedLines}
                          report={report}
                          sendBackData={sendBackData}
                          chartTitle={chartTitle}
                          years={years}
                          dateRange={dateRange}
                        />
                      </Col>
                    </div>
                  </Col>

                  <Col span={24} lg={12} className="gutter-row">
                    <div className="mid-content mt-4">
                      <Col span={24} className="gutter-row">
                        <Row className="d-inine-flex justify-content-between align-items-center mb-3">
                          <h6 className="font-weight-md6 my-3">
                            {MESSAGES_BASED_ON_URGENCY_LEVEL}
                          </h6>
                          <span>
                            <a
                              id="downloadDoughNutChart"
                              download="Messages-based-on-urgency-level.jpg"
                              href=""
                            >
                              <DownloadOutlined
                                className="font-18 cursor-pointer"
                                onClick={() =>
                                  downloadChartImage("doughNutChart")
                                }
                              />
                            </a>
                          </span>
                        </Row>
                        <Row>
                          <h6 className="font-weight-md6 my-3">
                            {TOTAL_MESSAGES}: {totalMessagesCount}
                          </h6>
                        </Row>
                        <DoughNutChart
                          urgencyLevels={urgencyLevels}
                          chartTitle={MESSAGES_BASED_ON_URGENCY_LEVEL}
                        />
                      </Col>
                    </div>
                  </Col>

                  <Col span={24} lg={12} className="gutter-row">
                    <div className="mid-content mt-4">
                      <Col span={24} className="gutter-row">
                        <Row className="d-inine-flex justify-content-between align-items-center mb-3">
                          <h6 className="font-weight-md6 my-3">
                            {RESOLVED_MESSAGES}
                          </h6>
                          <span>
                            <a
                              id="downloadPieChart"
                              download="Resolved-messages.jpg"
                              href=""
                            >
                              <DownloadOutlined
                                className="font-18 cursor-pointer"
                                onClick={() => downloadChartImage("pieChart")}
                              />
                            </a>
                          </span>
                        </Row>
                        <Row>
                          <h6 className="font-weight-md6 my-3">
                            {TOTAL_MESSAGES}: {totalMessagesCount}
                          </h6>
                        </Row>
                        <PieChart
                          resolvedMessages={closedConversationsCount}
                          totalMessages={totalMessagesCount}
                          chartTitle={RESOLVED_MESSAGES}
                        />
                      </Col>
                    </div>
                  </Col>
                </Row>
              </Spin>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};
export default Reports;
