import React, { useState } from "react";
import {
  Layout,
  Row,
  Col,
  Breadcrumb,
  Button,
  Form,
  Input,
  DatePicker,
  Select,
  Alert,
  Spin,
} from "antd";
import { NavLink, useHistory } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import "react-phone-input-2/lib/style.css";
import OrgSidebar from "../../containers/OrgSidebar";
import styles from "./manageEmp.module.scss";
import "../form/form.scss";
import FloatLabel from "../form/FloatLabels";
import PhoneInput from "react-phone-input-2";
import moment from "moment";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import authHeader from "../../services/auth-header";
import { ADD_EMPLOYEE, SAVE, CANCEL } from "../../headings";
import { ADD_EMPLOYEE_TEXT_PART4 } from "../../messages";
import {
  REQUIRED_MESSAGE,
  SELECT_DATE,
  SELECT_STATUS,
  SELECT_USER_TYPE,
  VALID_EMAIL,
  VALID_MOBILE,
} from "../../errors";
import {
  EMAIL,
  FIRST_NAME,
  LAST_NAME,
  MOBILE,
  STATUS,
  DATE,
  SELECT_ROLE,
  START_DATE,
  END_DATE
} from "../../labels";
import {
  STATUS_ACTIVE,
  STATUS_INACTIVE,
} from "../../values";
import MultiRoleSelection from './MultiRoleSelection';
import { DownOutlined } from "@ant-design/icons";

const { Content } = Layout;
const { RangePicker } = DatePicker;
const CreateManually = (props) => {
  const [userDetails, setUserDetails] = useState({
    firstName: "",
    lastName: "",
    status: "",
    email: "",
    //date: "",
    mobile: "",
    empId: "",
    department: "",
    role: "",
    startDate:"",
    endDate : ""
  });
  const history = useHistory();
  const [error, showError] = useState(false);
  const [loader, setLoader] = useState(false);
  const [emailValidationError, setEmailValidationError] = useState(false);
  const [mobileValidationError, setMobileValidationError] = useState(false);

  const handleUserOnChange = (e) => {
    setUserDetails({
      ...userDetails,
      [e.target.id]: e.target.value,
    });
  };

  const handleMobile = (mobile) => {
    setUserDetails({
      ...userDetails,
      mobile: mobile,
    });
  };

  const handleUserOnSelect = (value, name) => {
    setUserDetails({ ...userDetails, [name]: value });
  };

  const handleUserDatePickerChange = (date, name) => {
    setUserDetails({ ...userDetails, [name]: date });
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  const validateEmailAddress = (e) => {
    var re = /\S+@\S+\.\S+/;
    if (!re.test(e.target.value)) {
      setEmailValidationError(true);
    } else {
      setEmailValidationError(false);
    }
  };

  const validateMobile = (e) => {
    var regex = /^\+\d$/;
    if (userDetails.mobile.length > 10 || regex.test(e.target.value)) {
      setMobileValidationError(false);
    } else {
      setMobileValidationError(true);
    }
  };

  const onSubmit = () => {
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    if (
      userDetails.firstName &&
      userDetails.lastName &&
      userDetails.email &&
      userDetails.mobile &&
      userDetails.status &&
      //userDetails.role &&
      userDetails.startDate
    ) {
      const data = userDetails.endDate ? {
        first_name: userDetails.firstName,
        last_name: userDetails.lastName,
        email: userDetails.email,
        mobile_number: "+" + userDetails.mobile,
        is_org_cc_user: userDetails.role.includes('2') ? 1 : 0,
        is_idb_user: userDetails.role.includes('3') ? 1 : 0,
        is_super_user: userDetails.role.includes('1') ? 1 : 0,
        start_date: userDetails.startDate
          ? moment(userDetails.startDate).format("DD-MM-YYYY")
          : "",
        end_date: userDetails.endDate
          ? moment(userDetails.endDate).format("DD-MM-YYYY")
          : "",
        status_id: parseInt(userDetails.status),
      } : {
        first_name: userDetails.firstName,
        last_name: userDetails.lastName,
        email: userDetails.email,
        mobile_number: "+" + userDetails.mobile,
        is_org_cc_user: userDetails.role.includes('2') ? 1 : 0,
        is_idb_user: userDetails.role.includes('3') ? 1 : 0,
        is_super_user: userDetails.role.includes('1') ? 1 : 0,
        start_date: userDetails.startDate
          ? moment(userDetails.startDate).format("DD-MM-YYYY")
          : "",
        status_id: parseInt(userDetails.status),
      };
      showError(false);
      setLoader(true);
      axios.post(BASE_URL + "createUser/", data, options).then(() => {
        window.location = "/orgcc/manageemployees";
      });
    } else {
      showError(true);
    }
  };

  const onCancel = () => {
    history.push("/orgcc/addemployee");
  };

  const handleRoleOnSelect = (value) => {
    setUserDetails({ ...userDetails, role: value });
  };

  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser data={props} />
              <Content className="w-100">
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/orgcc/addemployee">
                      <Breadcrumb.Item>{ADD_EMPLOYEE}</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>{ADD_EMPLOYEE_TEXT_PART4}</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">{ADD_EMPLOYEE_TEXT_PART4}</h1>
                </Row>
                <div className="mid-content mt-4">
                  <Row className="d-flex justify-content-center align-items-center">
                    <Spin spinning={loader} size="large">
                      <Form className="input-md floating-label w-100" name="">
                        {error && (
                          <Form.Item>
                            <Alert
                              message={REQUIRED_MESSAGE}
                              type="error"
                              showIcon
                            ></Alert>
                          </Form.Item>
                        )}
                        <Col span={24}>
                          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={FIRST_NAME}
                                value={userDetails.firstName}
                              >
                                <Form.Item
                                  className="required"
                                  name="firstName"
                                >
                                  <Input
                                    className="input-control"
                                    id="firstName"
                                    htmlFor="firstName"
                                    onChange={(e) => handleUserOnChange(e)}
                                    autoComplete="off"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={LAST_NAME}
                                value={userDetails.lastName}
                              >
                                <Form.Item
                                  className="required"
                                  name="lastName"
                                  validateStatus="success"
                                >
                                  <Input
                                    className="input-control"
                                    id="lastName"
                                    htmlFor="lastName"
                                    onChange={(e) => handleUserOnChange(e)}
                                    autoComplete="off"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={EMAIL}
                                value={userDetails.email}
                              >
                                <Form.Item
                                  className="required"
                                  name="email"
                                  validateStatus="success"
                                >
                                  <Input
                                    type="email"
                                    className="input-control"
                                    id="email"
                                    htmlFor="email"
                                    value={userDetails.email}
                                    onChange={(e) => handleUserOnChange(e)}
                                    onBlur={(e) => validateEmailAddress(e)}
                                    autoComplete="off"
                                  />
                                  {emailValidationError && (
                                    <Alert
                                      message={VALID_EMAIL}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col
                              span={24}
                              md={8}
                              className="gutter-row phone-field"
                            >
                              <FloatLabel
                                label={MOBILE}
                                value={userDetails.mobile}
                              >
                                <Form.Item
                                  className="required"
                                  name="mobile"
                                  validateStatus="success"
                                >
                                  <PhoneInput
                                    country="us"
                                    id="mobile"
                                    htmlFor="mobile"
                                    value={userDetails.mobile}
                                    onChange={handleMobile}
                                    onBlur={validateMobile}
                                    autoComplete="off"
                                  />
                                  {mobileValidationError && (
                                    <Alert
                                      message={VALID_MOBILE}
                                      type="error"
                                      showIcon
                                    ></Alert>
                                  )}
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={STATUS}
                                value={userDetails.status}
                              >
                                <Form.Item
                                  className="required"
                                  name="status"
                                  rules={[
                                    {
                                      required: true,
                                      message: SELECT_STATUS,
                                    },
                                  ]}
                                >
                                  <Select
                                    className="input-control w-100"
                                    htmlFor="status"
                                    onSelect={(value) =>
                                      handleUserOnSelect(value, "status")
                                    }
                                  >
                                    <Option value="1">{STATUS_ACTIVE}</Option>
                                    <Option value="2">{STATUS_INACTIVE}</Option>
                                  </Select>
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            {/* <Col span={24} md={8} className="gutter-row">
                              <FloatLabel label={DATE} value="date">
                                <Form.Item
                                  className="input-control datepicker-wrapper required"
                                  rules={[
                                    {
                                      required: true,
                                      message: SELECT_DATE,
                                    },
                                  ]}
                                >
                                  <RangePicker
                                    className="w-100 "
                                    name="date"
                                    value={userDetails.date}
                                    onChange={(date) =>
                                      handleUserDatePickerChange(date, "date")
                                    }
                                    disabledDate={disabledDate}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col> */}
                            <Col span={24} md={4} className="gutter-row">
                              <FloatLabel label={START_DATE} value="date">
                                <Form.Item
                                  className="input-control datepicker-wrapper required"
                                  rules={[
                                    {
                                      required: true,
                                      message: SELECT_DATE,
                                    },
                                  ]}
                                >
                                  <DatePicker
                                    className="w-100 "
                                    name="startDate"
                                    value={userDetails.startDate}
                                    onChange={(date) =>
                                      handleUserDatePickerChange(date, "startDate")
                                    }
                                    disabledDate={disabledDate}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={4} className="gutter-row">
                              <FloatLabel label={END_DATE} value="date">
                                <Form.Item
                                  className="input-control datepicker-wrapper"
                                >
                                  <DatePicker
                                    className="w-100 "
                                    name="endDate"
                                    value={userDetails.endDate}
                                    onChange={(date) =>
                                      handleUserDatePickerChange(date, "endDate")
                                    }
                                    disabledDate={disabledDate}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            {/* <Col span={24} md={8} className="gutter-row">
                              <FloatLabel
                                label={SELECT_ROLE}
                                value={userDetails.role}
                              >
                                <Form.Item
                                  name="role"
                                >
                                  <Select
                                    className="input-control w-100"
                                    htmlFor="role"
                                    onSelect={(value) =>
                                      handleUserOnSelect(value, "role")
                                    }
                                  >
                                    <Option value="1">{MAKE_SUPER_USER}</Option>
                                    <Option value="2">{MAKE_ORG_USER}</Option>
                                    <Option value="3">{MAKE_IDB_USER}</Option>
                                  </Select>
                                </Form.Item>
                              </FloatLabel>
                            </Col> */}

                            <Col span={24} md={8} className="gutter-row">
                              <MultiRoleSelection
                                handleRoleOnSelect={handleRoleOnSelect}
                              />
                              <DownOutlined className="down-arrow-ico" />
                            </Col>
                          </Row>
                        </Col>

                        <Form.Item>
                          <Button
                            type="primary"
                            htmlType="submit"
                            className="mr-4"
                            onClick={onSubmit}
                          >
                            {SAVE}
                          </Button>
                          <Button type="secondary" onClick={onCancel}>
                            {CANCEL}
                          </Button>
                        </Form.Item>
                      </Form>
                    </Spin>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default CreateManually;
