import React, { Component } from "react";
import { Modal, Button, Row, Divider } from "antd";
import "../modal/modal.scss";
import MapContainer from "./GMap"

class Location extends Component {
  state = { visible: false };
  showModal = () => {
    this.setState({
      visible: true,
      latitude:"",
      longitude :"",
      locationName:""
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
    this.props.setLocation(this.state.longitude, this.state.latitude, this.state.locationName)
  };

  getMapLocation=(lag, lat)=>{
    console.log(lag,"===", lat);
    this.setState({latitude:lat,longitude:lag})
  }

  getCurrenLocationName=(locationname)=>{
    console.log("locationname", locationname)
    this.setState({locationName:locationname})
  }

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <>
       
        <Button
          type="link"
          className="upload-list link pl-0"
          onClick={this.showModal}
        >
          <span className="icon icon-location"></span> Location
        </Button>

        <Modal
          title="Select Location"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={800}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
            className: "ant-btn-secondary",
          }}
          okText="Add"
          cancelText="Cancel"
        >
          <Row className="py-4 map-container">
            <MapContainer      
             getMapLocation={this.getMapLocation}
             getCurrenLocationName={this.getCurrenLocationName}
             />
          </Row>
        </Modal>
      </>
    );
  }
}

export default Location;
