import React, { useState } from "react";
import { Layout, Row, Col, Button, Input, Select, Divider, Upload, Image} from "antd";
import { Link } from "react-router-dom";
import ReactHtmlParser from "react-html-parser";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import BroadcastPopover from "../table/BroadcastPopover";
import moment from "moment";
const fileobj=[{
  
    uid: '-1',
    name: 'image.png',
    status: 'done',
    url: 'https://amsstagingresources.s3-us-west-1.amazonaws.com/broadcast-icons/2/cf1a4b54-0eb0-4724-9dc9-e766996c7a5a.jpeg',
    thumbUrl : 'https://amstagingresources.s3-us-west-1.amazonaws.com/broadcast-icons/2/cf1a4b54-0eb0-4724-9dc9-e766996c7a5a.jpeg',

  
}]

const { Content } = Layout;

const { Search } = Input;
const BroadcastMessages = ({setFilterOptions,setSearchValue,setSortOptions, data,broadcastmessages}) => {

  const handleFilterOnSelect =(value) =>{
    console.log(value);
    let newArray=[];
    if(value){
      newArray = [parseInt(value)];
    }
    const data = {
      status_id: newArray,
    };
    setFilterOptions(data);
  }
  const onSearch = (value) =>{
    setSearchValue(value)
  }

  const onSort = (value) =>{
    setSortOptions(value)
  }
   return (
     
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser />
              <Content>
                <Row className="py-3 justify-content-between">
                  <h1 className="mb-0">Broadcast Messages</h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3 px-3 px-lg-0 sm-direction-reverse">
                    <Col span={24} lg={18} className="search-row mt-2 mt-lg-0">
                      <Row>
                        <Col span={24} lg={9} className="search-box">
                          <Search placeholder="Search"
                           onSearch={onSearch} />
                        </Col>
                        <Col
                          span={24}
                          lg={6}
                          className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                        >
                          <Select
                            className="w-100 w-md-75"
                            defaultValue="Filter"
                            onSelect={(value)=>handleFilterOnSelect(value)}
                            >
                            <Option value="">All</Option>
                            <Option value="7">Scheduled</Option>
                            <Option value="9">Broadcasted</Option>
                          </Select>
                        </Col>
                        <Col
                          span={24}
                          lg={6}
                          className="ml-0 ml-lg-4 mt-2 mt-lg-0"
                        >
                          <Select
                            className="w-100 w-md-75"
                            defaultValue="Sort"
                            onSelect={(value)=>onSort(value)}
                            >
                            <Option value="DESC">Newest at top</Option>
                            <Option value="ASC">Oldest at top</Option>
                          </Select>
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={24}
                      lg={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Link to="/orgcc/createbroadcast">
                      <Button type="primary">Create Broadcast</Button>
                      </Link>
                    </Col>
                  </Row>
                </Col>
                <div className="mid-content">
                  {
                   broadcastmessages?.map((value)=>(
                    <Row>
                      <Col span={22}>
                        <h6 className="font-weight-md mb-1">{value.title}</h6>
                        { value.status_id === "7"?(
                        <small>
                          {"Scheduled On : "}
                          {moment(value.scheduled_date).utc().local().format("MM/DD/YYYY")}
                        </small>
                        ):(
                          <small>
                          {"BroadCasted On : "}
                          {moment(value.scheduled_date).utc().local().format("MM/DD/YYYY")}
                        </small>
                        )}
                        <p className="pt-2">
                        {ReactHtmlParser(value.message)}
                        </p>
                        {value.broadcast_images!=null?(
                           value.broadcast_images?.map((item)=>(
                                <Image
                                  width={100}
                                  src={item.url}
                                  className="pr-3"
                                  placeholder={item.name}
                                  alt={item.name}
                                  />
                            )) 
                          ):(null)
                        }
                        {value.broadcast_documents!=null?(
                           value.broadcast_documents?.map((item)=>(
                             <div className="attachment-wrapper">
                                <span className="icon icon-doc pr-2"/>
                                <a href={item.url} download={item.name}>{item.name}</a>                                
                               </div>
                            )) 
                          ):(null)
                        }
                     </Col>
                      <Col span={2} className="d-flex justify-content-end align-items-start">
                        <BroadcastPopover data={value}/>
                      </Col>
                      <Divider className="w-100 mt-2" />
                     
                    </Row>
                   ))
                  }
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default BroadcastMessages;
