import axios from "axios";
import { BASE_URL } from "../userconfig";
import authHeader from "./auth-header";
import * as AppStorage from "../utils/AppStorage";

var CryptoJS = require("crypto-js");
const token = authHeader();

const options = {
  headers: {
    Authorization: token,
    Accept: "application/json, text/plain",
    "Content-Type": "application/json;charset=UTF-8",
  },
};
class UserService {
  orgId() {
    const orgcc_user = JSON.parse(AppStorage.get("user"));
    if (orgcc_user && orgcc_user.user_data.organization_id) {
      const orgId = CryptoJS.AES.decrypt(
        orgcc_user.user_data.organization_id,
        "anonymoose"
      ).toString(CryptoJS.enc.Utf8);
      return parseInt(orgId);
    } else {
      return {};
    }
  }
  getOrganizationData() {
    return axios.get(BASE_URL + "getOrganization/", options);
  }

  getEmployees(filterStatus, search) {
    const organizationId = this.orgId();
    if (Object.keys(filterStatus).length > 0 && search.length > 0) {
      return axios.get(
        BASE_URL +
          "getEmployees/" +
          organizationId +
          "?filter=" +
          JSON.stringify(filterStatus) +
          "&search=" +
          search,
        options
      );
    } else if (Object.keys(filterStatus).length > 0 && search.length === 0) {
      return axios.get(
        BASE_URL +
          "getEmployees/" +
          organizationId +
          "?filter=" +
          JSON.stringify(filterStatus),
        options
      );
    } else if (
      (Object.keys(filterStatus).length === undefined ||
        Object.keys(filterStatus).length === 0) &&
      search.length > 0
    ) {
      return axios.get(
        BASE_URL + "getEmployees/" + organizationId + "?search=" + search,
        options
      );
    } else {
      return axios.get(BASE_URL + "getEmployees/" + organizationId, options);
    }
  }

  getHotlines() {
    return axios.get(BASE_URL + "hotline/", options);
  }

  getOpenlines() {
    return axios.get(BASE_URL + "openline/", options);
  }

  getBroadCastMessages(filterStatus, search, sort) {
    console.log(filterStatus, search, sort)
    if (filterStatus.status_id?.length > 0 && search.length > 0 && sort.length >0) {

      return axios.get(BASE_URL + "broadcast/?filter=" +
        JSON.stringify(filterStatus) +
        "&search=" +
        search +
        "&sort=" +
        sort,
        options
      );
    } else if (filterStatus.status_id?.length > 0 &&  search.length === 0) {
    
      return axios.get(
        BASE_URL + "broadcast/?filter=" + JSON.stringify(filterStatus),
        options
      );
    }else if (
       (filterStatus.status_id?.length === undefined ||
        filterStatus.status_id?.length === 0) &&
      search.length > 0
    ) {
      return axios.get(BASE_URL + "broadcast/?search=" + search, options);
    }else if (
      (filterStatus.status_id?.length === undefined ||
       filterStatus.status_id?.length === 0) &&
       search.length === 0 && sort.length>0
     ) { 
      return axios.get(BASE_URL + "broadcast/?sort=" + sort, options);

     }else {
      return axios.get(BASE_URL + "broadcast/", options);

    }
  }

  getPolls(filterStatus, search) {
    if (filterStatus.status_id?.length > 0 && search.length > 0) {
      return axios.get(
        BASE_URL +
          "poll/?filter=" +
          JSON.stringify(filterStatus) +
          "&search=" +
          search,
        options
      );
    } else if (filterStatus.status_id?.length > 0 && search.length === 0) {
      return axios.get(
        BASE_URL + "poll/?filter=" + JSON.stringify(filterStatus),
        options
      );
    } else if (
      (filterStatus.status_id?.length === undefined ||
        filterStatus.status_id?.length === 0) &&
      search.length > 0
    ) {
      return axios.get(BASE_URL + "poll/?search=" + search, options);
    } else {
      return axios.get(BASE_URL + "poll/", options);
    }
  }
  getAssignHotlines(hotLineId) {
    return axios.get(BASE_URL + "getHotlineAssignEmpl/" + hotLineId, options);
  }
}

export default new UserService();
